
.sk-table-container .mu-text-field-content {
  padding-bottom: 0px;
  padding-top: 0px;
}
.sk-table-container .mu-text-field {
  min-height: 10px;
  margin-bottom: 0px;
}
.sk-table-container .mu-text-field-input {
  border: thin solid #ddd;
  padding: 8px;
  height: 36px;
  font-size: 14px;
  line-height: 24px;
  border-radius: 2px;
}
.sk-table-container .mu-text-field-input:focus {
  border-color: #333;
  box-shadow: 0px 0px 11px -3px #000;
}
.sk-table-container .mu-text-field-line, .sk-table-container .mu-text-field-focus-line {
  display: none;
}
.sk-table-container .mu-text-field-help {
  display: none;
}
.sk-table-container .has-error-editor .mu-text-field-input {
  border-color: #e00;
  box-shadow-color: #e00;
}
.sk-table-container .page-size-select {
  float: left;
  width: 150px;
  text-align: center;
}
.sk-table-container .sk-td-content {
  word-break: break-all;
  max-width: 100%;
  white-space: normal;
}



